table.datatable {
	width: 100%;
	padding: 0;
	margin-bottom: 1.5rem;
}
table.static-datatable {
	width: 100% !important;
}
.inline-datatable-conatiner {
	display: grid;
	/* grid-template-columns: 1fr 1fr auto; */
	grid-auto-flow: column;
	grid-gap: 1.5rem;
}
.datatable th,
.datatable td {
	background-color: #fff;
	border: 1px solid #383838;
	white-space: nowrap;
}
.datatable th {
	font-size: 0.8rem;
	letter-spacing: 1px;
	background-color: #383838;
	color: #ffffff;
	padding: 0.3rem 0.1rem;
}
.datatable td {
	font-weight: 800;
	min-width: 1rem;
	height: 1rem;
	padding: 0;
	margin: 0;
	-webkit-box-sizing: content-box;
	        box-sizing: content-box;
}
.td-txt-right td,
.td-txt-right input {
	text-align: right !important;
}

.filled-txt {
	background-color: var(--background-offset);
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	height: inherit;
	width: inherit;
	padding: 0.6rem 0.2rem;
}
.static-datatable .filled-txt {
	/* min-width: 1rem; */
	/* min-height: 2rem; */
	font-size: 0.7rem;
	background-color: var(--background) !important;
}
.auto-filled-txt {
	background-color: #e0f0ad;
}
.span-prefix {
	color: var(--txt-offset);
	font-size: 0.9rem;
	padding-right: 0.2rem;
	font-weight: 700;
	z-index: 3;
}
.span-suffix {
	color: var(--txt-offset);
	font-size: 0.9rem;
	padding: 0 0.3rem;
	padding-left: 0.2rem;
	font-weight: 700;
	z-index: 3;
}
.span-input-prefix {
	color: var(--txt-offset);
	font-size: 0.9rem;
	padding-left: 0.3rem;
	margin-right: -0.8rem;
	font-weight: 700;
	z-index: 3;
}
.span-input-prefix + .input-prefix {
	padding-left: 1.1rem !important;
}
.span-input-prefix-long {
	color: var(--txt-offset);
	font-size: 0.9rem;
	padding-left: 0.3rem;
	margin-right: -2rem;
	font-weight: 700;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	z-index: 3;
}
.span-input-prefix-long + .input-prefix {
	padding-left: 2.3rem !important;
}
.span-input-suffix {
	color: var(--txt-offset);
	font-size: 0.9rem;
	padding: 0 0.3rem;
	padding-left: 0.2rem;
	margin-left: -1.2rem;
	font-weight: 700;
	z-index: 3;
}
/* Input */
table.datatable td input {
	background-color: #ffffff;
	font-weight: 600;
	width: 100%;
	height: 100%;
	padding: 0.3rem;
	border-width: 0;
	/* text-align: right; */
	cursor: cell;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	outline: none;
	-webkit-transition: -webkit-box-shadow 100ms;
	transition: -webkit-box-shadow 100ms;
	-o-transition: box-shadow 100ms;
	transition: box-shadow 100ms;
	transition: box-shadow 100ms, -webkit-box-shadow 100ms;
}
table.datatable td input:hover {
	position: relative;
	border-radius: 0.1rem;
	-webkit-box-shadow: 0 0 0 0.2rem #797979;
	        box-shadow: 0 0 0 0.2rem #797979;
	z-index: 2;
}
table.datatable td input:focus {
	text-align: left;
	-webkit-box-shadow: inset 0 0 0 0.2rem #d1d1d1;
	        box-shadow: inset 0 0 0 0.2rem #d1d1d1;
	z-index: 1;
}
.editable-table .DayPickerInput {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row-reverse;
	    flex-direction: row-reverse;
	width: 100%;
	margin: 0 !important;
	height: 1.8rem;
	padding: 0 !important;
}
.editable-table .DayPickerInput input {
	background-color: transparent;
	min-width: 5rem;
	width: 100%;
	height: 100% !important;
	padding: 1rem 0.5rem !important;
	margin: 0 !important;
	margin-top: -0.1rem !important;
	border-width: 0;
	/* text-align: right; */
	cursor: cell;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	outline: none;
	-webkit-transition: -webkit-box-shadow 100ms;
	transition: -webkit-box-shadow 100ms;
	-o-transition: box-shadow 100ms;
	transition: box-shadow 100ms;
	transition: box-shadow 100ms, -webkit-box-shadow 100ms;
}
.editable-table .DayPickerInput input:hover {
	position: relative;
	border-radius: 0.1rem;
	-webkit-box-shadow: 0 0 0 0.2rem #797979;
	        box-shadow: 0 0 0 0.2rem #797979;
	z-index: 2;
}
.editable-table .DayPickerInput input:focus {
	text-align: left;
	-webkit-box-shadow: inset 0 0 0 0.2rem #d1d1d1;
	        box-shadow: inset 0 0 0 0.2rem #d1d1d1;
	z-index: 1;
}
.DayPickerInput-Overlay {
	-webkit-box-shadow: var(--elevation-12);
	        box-shadow: var(--elevation-12);
}
/* Prefix and Suffix */
.input-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: center;
	    align-items: center;
	width: 100%;
	height: 100%;
}
.input-prefix {
	padding-left: 1.2rem !important;
	width: calc(100% - 1rem);
	min-width: 2rem;
}
.input-suffix {
	padding-right: 1.2rem !important;
	width: calc(100% - 1rem);
	min-width: 2rem;
}

/* Selet input */

.datatable td select {
	background-color: #ffffff;
	min-width: 5rem;
	width: 100%;
	height: 1.8rem;
	padding: 0.3rem;
	border-width: 0;
	/* text-align: right; */
	cursor: cell;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	outline: none;
	-webkit-transition: -webkit-box-shadow 100ms;
	transition: -webkit-box-shadow 100ms;
	-o-transition: box-shadow 100ms;
	transition: box-shadow 100ms;
	transition: box-shadow 100ms, -webkit-box-shadow 100ms;
}
.datatable td select:hover {
	position: relative;
	border-radius: 0.1rem;
	-webkit-box-shadow: 0 0 0 0.2rem #797979;
	        box-shadow: 0 0 0 0.2rem #797979;
	z-index: 2;
}
.datatable td select:focus {
	text-align: left;
	-webkit-box-shadow: inset 0 0 0 0.2rem #d1d1d1;
	        box-shadow: inset 0 0 0 0.2rem #d1d1d1;
	z-index: 1;
}

/* Remove number input arrow */
.datatable td input[type="number"] {
	-moz-appearance: textfield;
}
.datatable td input[type="number"]::-webkit-inner-spin-button,
.datatable td input[type="number"]::-webkit-outer-spin-button {
	-webkit-appearance: none;
	-moz-appearance: textfield;
	margin: 0;
}
.datatable td input[type="date"] {
	width: 100%;
	font-size: 2vw;
	padding: 0px;
	padding-left: 2px !important;
	padding-right: 2px !important;
	margin: 0;
	border-width: 0;
	-moz-appearance: textfield;
}
.datatable td input[type="date"]::-webkit-inner-spin-button,
.datatable td input[type="date"]::-webkit-outer-spin-button {
	-webkit-appearance: none;
	-moz-appearance: textfield;
	margin: 0;
}



